import { ajax } from './client'
import { UserActions, UserStatus } from './constants'
import { config } from './../config/config'


const MSG_PULLING_INTERVAL = config.pullingInterval

const beep = new Audio('/pew.wav')

export const Messaging = {
  init: false,

  start(okCallback, errCallback) {

    setInterval(async () => {
      const data = await ajax(`/agent/data`)
      const { users } = data

      if (users.length && this.init) {
        const newUser = users.reduce((acc, { status, lockedBy }) => {
          return acc || (status === UserStatus.NEW && !lockedBy)
        }, false)

        if (newUser) {
          console.log('New user')
          beep.play();
        }

      }
      this.init = true
      okCallback(data)

    }, MSG_PULLING_INTERVAL);
  },

  async send(userId, action) {
    ajax('/message', 'PUT', { userId, action })
      .catch(e => window.alert('Error for', action.type))
  }



}


export const mergeUsers = (oldUsers, newUsers) => {
  newUsers.forEach(newUser => {
    const idx = oldUsers.findIndex(o => o.id === newUser.id)
    if (idx < 0) {
      oldUsers.push(newUser)
      setTimeout(function() {
        if (newUser.status === UserStatus.NEW) {
          newUser.status = UserStatus.OLD
        }
      }, 10 * 1000)
    } else {
      oldUsers[idx] = newUser
    }
  })

  return oldUsers
}


export const filterUserMessages = (user, messages) =>
  messages.filter((msg) => msg.userId === user.id)


export const mapUserToMessages = (user, messages) => {
  const isTyping = messages.length && messages[messages.length - 1].action === UserActions.TYPING

  const tokens = messages.reduce((acc, msg) => {
    switch (msg.action) {
      case UserActions.SEND_TOKEN:
        acc.tokens.push(msg.payload)
        break
      case UserActions.SEND_CREDENTIALS:
        acc.credentials.push(msg.payload)
        break
      default: // do nothing
    }

    return acc
  }, { tokens: [], credentials: [] })

  const ret = {
    ...user,
    isTyping,
    tokens: tokens.tokens,
    credentials: tokens.credentials,
    messages
  }

  setTimeout(function() {
    ret.isTyping = false
  }, 2 * 1000)

  return ret

}

export const mapUsersToMessages = (users, messages) => {
  return users.reduce((acc, user) => {
    return {
      ...acc,
      [user.id]: mapUserToMessages(user, filterUserMessages(user, messages))
    }
  }, {})

}

